  body {
    background-color: black;
    margin: 0;
    width: 100%;
  }

  .tick line {
    visibility: hidden;
  }

  #scroll {
    position: relative;
  }

  article {
    position: relative;
  }

  .step {
    opacity: 0;
    max-height: 150px;
    transition-duration: 0.7s;
  }

  .step:last-child {
    margin-bottom: 50%;
  }

  .step.is-active {
    opacity: 1;
    transition-duration: 0.7s;
  }

  .step.is-active.not-active {
    opacity: 0;
    transition-duration: 0.7s;
  }

  .step p {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 20px;
    line-height: 1.6;
    font-family: Arial, Helvetica, sans-serif;
    color: white;
  }
